import React from "react";
import styled from "styled-components";
import Button from "./Button";

const Buttons = styled(Button)`
  margin-top: 20px;
  margin-left: 0%;
  &:hover {
    color: white;
    background-color: black;
  }
`;
const Wrapper = styled.div`
  display: block;
  align-items: center;
  justify-content: center;
  text-align: flex-start;
  &: @screens (max-width: 680px) {
    msrgin-left: 5px;

    height: 100px;
    width: 700px;
  }
`;

const Img = styled.img`
  position: absolute;
  margin-top: -300px;
  margin-left: 9%;
  align-items: Center;
  width: 80%;
  height: 600px;
  & @media (max-width:680px) {
    margin-top: 100px;
    height: 500px;
    width: 80%;
  }
`;
const Dat = styled.h2`
  width: 850px;
  position: absolute;
  margin-top: -85px;
  margin-left: 200px;
  text-align: flex-end;
  font-size: 44px;
  font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial;
  font-weight: 500;
  line-height: 52px;
  color: white;
  & @screens(max-width:680px) {
    height: 400px;
    width: 300px;
    font-weight: normal;
    line-height: 24px;
  }
`;
const Text = styled.p`
  width: 850px;
  position: absolute;
  margin-left: 200px;
  text-align: flex-end;
  font-size: 20px;
  font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial;
  font-weight: 500;
  line-height: 24px;
  color: white;
  & @screens(max-width:680px) {
    height: 400px;
    width: 300px;
    font-weight: normal;
    line-height: 24px;
  }
`;

const Six = () => {
  return (
    <Wrapper>
      <Img src="https://blog.ecabrella.com/hubfs/how-does-uber-frieght-work.png" />
      <Dat>Direct access to almost immediate capacity </Dat>
      <Text>
        {" "}
        Our single platform gives Land Oâ€™Lakes the confidence and ability to
        move freight efficiently.
        <Buttons> Play Video</Buttons>
      </Text>
    </Wrapper>
  );
};
export default Six;
