import React from "react";

import Button from "./Button";
const Main = (props) => {
  return (
    <div className="info">
      <div className="info-left">
        <section className="name">
          <h2 className="po">{props.title}</h2>
          <h3 className="cow">{props.para}</h3>
          <div>
            <Button primary>Share with Uber</Button>
            <Button>Haul with Uber</Button>
          </div>
        </section>
      </div>
      <div className="info-right">
        <img src="https://www.uber-assets.com/image/upload/f_auto,q_auto:eco,c_fill,w_698,h_872/v1579208077/assets/e9/8f8233-84ac-4f1d-a322-fb0fe17e96c9/original/1.-Hero---Desktop.png" />
      </div>
    </div>
  );
};
export default Main;
