import styled from "styled-components";
import Button from "./Button";

const Jam = styled.div`
  width: 100%;
  height: 450px;
  margin-left: 10%;
  display: grid;
  grid-gap: 20px;
  grid-column-template: auto(350px, 1fr);
  &.@media(max-width:680px) {
    display: block;
    height: 100%;
    width: 700px;
  }
`;
const Goa = styled.div`
  display: block;
  margin: 0px 20px;
  padding: 10px;

  width: 50%;
  float: left;
  text-align: flex-start;
  align-items: center;
  justify-content: center;
`;
const Tall = styled.h1`
  margin-top: -10%;
  font-size: 44px;
  font-weight: 500;
  width: 400px;
  color: black;
  font-family: UberMove, UberMoveText, system-ui, "Helvetica Neue", Helvetica,
    Arial, sans-serif;
  line-height: 52px;
`;
const Para = styled.p`
  font-size: 18px;
  font-weight: normal;
  color: black;
  font-family: UberMove, UberMoveText, system-ui, "Helvetica Neue", Helvetica;
  align-items: flex-start;
  justify-content: center;
  text-align: justify;
`;
const Ship = styled.div`
  display: flex;
  width: 400px;
  height: 50px;
  align-items: flex-start;
`;
const Buttonw = styled(Button)`
  font-family: UberMove, UberMoveText, system-ui, "Helvetica Neue", Helvetica;
  background: ${(props) => (props.primary ? "black" : "eeeeee")};
  color: ${(props) => (props.primary ? "white" : "black")};
  value: ${(props) => (props.text ? "Learn More" : "")};
  &:hover {
    background-color: ${(props) =>
      props.primary ? "rgb(32, 36, 36)" : "eeeeee"};
  }
  margin: 30px 50px;
  margin-left: -1%;
`;

const Erl = styled.div`
  flex: right;
  margin-top: -30%;
  margin-left: 40%;
  height: 200px;
  width: 30%;
`;

const Eight = () => {
  return (
    <Jam>
      <Goa>
        <Tall>Sign up for free and get started</Tall>
        <Para>
          Begin booking or scheduling the loads you want, 24 hours a day.
        </Para>
        <Ship>
          <Buttonw primary>Shipper signup</Buttonw>
          <Buttonw>Carrier signup</Buttonw>
        </Ship>
      </Goa>
      <Erl>
        <img
          src="https://www.uber-assets.com/image/upload/f_auto,q_auto:eco,c_fill,w_698,h_465/v1579208728/assets/a6/eb83af-401e-4882-a0ef-9dc3c4905f66/original/6.-Sign-Up-Image.png"
          width="600px"
        />
      </Erl>
    </Jam>
  );
};
export default Eight;
