import styled from "styled-components";

export default styled.div`
  display: Flex;
  height: 350px;
  width: 1200px;
  .Transparency {
    display: block;
    width: 330px;
    margin: 200px -30px 0 140px;
  }
  .Transparency-h1 {
    width: 330px;
    display: block;
    font-size: 18px;
    line-height: 32px;
    font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial,
      sans-serif;
    color: black;
    margin: 20px px 15px;
    align-items: center;
    width: 100%;
  }
  .Img {
    height: 22px;
    width: 7%;
    align-items: flex-start;
  }
  .Transparency-h2 {
    width: 330px;
    font-size: 16px;
    line-height: 32px;
    font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial,
      sans-serif;
    color: black;
    margin: 20px -2px;
    font-weight: normal;
    text-align: flex-start;
  }
`;
