import styled from "styled-components";

export default styled.button`
  background: ${(props) => (props.primary ? "black" : "eeeeee")};
  color: ${(props) => (props.primary ? "white" : "black")};
  value: ${(props) => (props.text ? "Learn More" : "")};
  &:hover {
    background-color: ${(props) =>
      props.primary ? "rgb(32, 36, 36)" : "eeeeee"};
  }
  width: 150px;
  height: 50px;
  line-height: 20px;
  font-size: 18px;
  margin-right: 20px;

  border: none;
  border-radius: 2px;
  text-align: center;

  front-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial,
    sans-serif;
`;
