import styled from "styled-components";
import Button from "./Button";

const Container = styled.div`
  display: grid;
  width: 100%;
  height: 100%;
  margin: 25px;
  grid-gap: 15px;
  &.@screens(max-width:700px) {
    align-items: left;
    justify-content: center;
    display: block;
    width: 700px;
    height: 100%;
  }
`;

const Tea = styled.div`
  flex: 50%;
  width: 40%;
  height: 750px;
  margin-top: 350px;
  margin-left: 10%;
`;
const Img = styled.img`
  width: 80%;
  height: 80%;
  margin-left: 20px;
  &.@media(max-width:680px) {
    width: 100%;
    height: 100%;
  }
`;

const Spill = styled.div`
  display: block;
  paddng: 10px;
  align-items: center;
  justify-content: center;
  flex: 50%;
  float: right;
  margin-left: 40%;
  margin-top: -52%;
  height: 600px;
  font-family: UberMove, UberMoveText, system-ui, "Helvetica Neue", Helvetica,
    Arial, sans-serif;
  &.@screens(max-width:700px) {
    width: 450px;
    margin-top: 100%;
    height: 400px;
    float: left;
    font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial,
      sans-serif;
  }
`;

const Head = styled.h1`
  width: 550px;
  margin-left: 200px;
  text-align: flex-start;
  font-size: 44px;
  font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial, ,
    sans-serif;
  font-weight: 500;
  line-height: 52px;
  color: black;
  &.@screens(max-width:680px) {
    height: 400px;
    width: 300px;
    font-weight: normal;
    line-height: 24px;
  }
`;
const Text = styled.p`
width: 550px;
margin-left: 200px;
text-align: justify;
font-size: 18px;
font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial, sans-serif;
font-weight: normal;
line-height: 24px;
color: black;
&. @screens(max-width:680px) {
    float:left;
  height: 400px;
  width: 550px;
  font-weight: normal;
  line-height: 24px;
`;
const Buttone = styled(Button)`
  color: white;
  background-color: black;
  margin-left: 200px;
  font-family: UberMove, UberMoveText, system-ui, "Helvetica Neue", Helvetica,
    Arial, sans-serif;
  &:hover {
    color: black;
    background-color: lightgray;
  }
`;

const Seven = () => {
  return (
    <Container>
      <Tea>
        <Img src="https://www.uber-assets.com/image/upload/f_auto,q_auto:eco,c_fill,w_698,h_872/v1580406387/assets/b5/f812fb-f3d4-4dde-a51d-9c09f59a2cc6/original/UF_Facility_Report_2X_700x800_V2.png" />
      </Tea>
      <Spill>
        <Head>Facility Insights Report </Head>
        <Text>Visibility into every part of your supply chain.</Text>

        <Text>
          Facility ratings, a feature in the Uber Freight app, empowers carriers
          and their drivers to share feedback and ratings of the pickup and
          delivery locations they visit.
        </Text>
        <Text>
          Our Facility Insights Report is an analysis of this data, providing
          shippers of every size with actionable insights to build supply chains
          that are more efficient.
        </Text>
        <Buttone> Get the report</Buttone>
      </Spill>
    </Container>
  );
};
export default Seven;
