import React from "react";
import { FaUser, FaGlobe } from "react-icons/fa";
import { IconContext } from "react-icons/lib";

const Navbar = () => {
  return (
    <IconContext.Provider value={{ margin: " 0 0 0 20px", size: "1.2em" }}>
      <div className="nav">
        <div className="nav-left">
          <div className="links">
            <a href="Uber-Freight">
              {" "}
              <button className="uber"> Uber Freight </button>
            </a>
            <a href="Carrier">Carrier</a>
            <a href="Shipper" className="shipper">
              {" "}
              Shipper
            </a>
            <a href="Blog" className="blog">
              {" "}
              Blog
            </a>
            <a href="Support" className="support">
              {" "}
              Support
            </a>
            <a href="Covid-19" className="covid">
              {" "}
              Covid-19 Resources
            </a>
          </div>
        </div>
        <div className="nav-right">
          <div className="links">
            <a href="EN" className="EN">
              {" "}
              <FaGlobe /> EN
            </a>
            <a href="Products"> Products</a>
            <a href="Log-in" className="log">
              {" "}
              <FaUser /> Log in
            </a>
            <a href="Sign-up">
              {" "}
              <button className="sign"> Sign up</button>
            </a>
          </div>
        </div>
      </div>
    </IconContext.Provider>
  );
};

export default Navbar;
